<template>
    <div class="ui-block">
        <div class="ui-block-content">
            <router-link to="/" style="color:#00AEEF" class="title">Back</router-link>
        </div>
        <div class="ui-block-title">
            <h6 class="title">All Deals</h6>
        </div>
        <div ref="related_deals">
            <div v-if="offers.length > 0 " class="row pl-3 pr-3">
                <div
                        v-for="(offer, index) in offers"
                        :key="index"
                        class="col-4"
                >
                    <router-link :to="{name: 'deal_page', params: {deal_id: offer.id } }">
                        <article class="hentry blog-post">
                            <div class="post-thumb mxh-125">
                                <img :src=" offer.file === null ? $store.state.staticImage : offer.file " alt="photo" />
                            </div>
                            <div class="post-content">
                                <a href="javascript:;" class="post-category bg-mdd">{{ offer.type }}</a>
                                <a href="javascript:;" class="h4 post-title">{{ $substring(offer.name) }}</a>

                                <div class="author-date">
                                    Deal Date
                                    <a class="h6 post__author-name fn" href="#">{{ getTime(offer.deal_date) }}</a>
                                </div>
                            </div>
                        </article>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "related_deals.vue",
        data() {
            return {
                offers: {},
                offers_count: ""
            }
        },
        mounted() {
            this.getRecommended();
        },
        methods: {
            showLoading() {
                let loader = this.$loader(this, this.$refs.related_deals, {
                    loaderType: "spinner"
                });
                return loader;
            },
            getTime(date) {
                // for this function to be used anywhere just call it as such
                // this.$getDateYear(date), to add extra functions use like so
                // this.$getDateYear(date,{ hour: true, min: true, sec: true })
                // this sets hour to true (to show the hour), same for min and sec
                // set timestamp option if date is in timestamp
                return this.$getDateYear(date,{ hour: true, min: true, sec: true })
            },
            // get all deals
            getRecommended() {
                let loader = this.showLoading();
                this.axios
                    .get("timeline/offers/recommended")
                    .then(response => {
                        loader.hide();
                        var res = response.data.data;
                        var offers = res.offers;
                        console.log('Offers',offers);
                        this.offers_count = offers.length;
                        this.offers = offers;
                        // console.log(hotels[0].photos.html_attributions);
                    })
                    .catch(error => {
                        loader.hide();
                        // console.log(error.response);
                        return error;
                    });
            },
        },
    }
</script>

<style scoped>
    .st {
        padding: 7px 10px;
        color: #fff !important
    }
</style>